body {
  background-color: #161a1d !important;
  color: #a9d6e5 !important;
}

* {
  padding: 0;
  margin: 0;
}

.box {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #eee;
  background-image: url("https://i.ibb.co/tHVHkRF/cd87311e-6e9f-4303-8b22-9946d42eb899.jpg");
  background-size: cover;
}

.card {
  border: double 7px #89c2d9 !important;
  background-color: #013a63 !important;
}
.card-title {
  font-size: 45px !important;
  font-weight: bold !important;
  font-family: Georgia, "Times New Roman", Times, serif !important;
}
.fsd {
  font-size: 30px !important;
  font-weight: bold !important;
  font-family: Georgia, "Times New Roman", Times, serif !important;
  text-shadow: 3px 3px 15px #a9d6e5;
}

.about {
  font-size: 22px !important;
  font-weight: bold !important;
  font-family: Georgia, "Times New Roman", Times, serif !important;
  color: #89c2d9 !important;
}

#blueBar {
  width: 100%;
  min-height: 32px;
  background-color: #222831;
  color: white;
  position: relative;
  font-weight: bold;
  font-size: 12pt;
}

.alter {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  animation: spin 10s linear infinite normal ;
  font-size: 12px;
}

.one {
  z-index: -1;
  animation-delay: 0s;
}

.two {
  animation-delay:2.5s;
  z-index:-1;
}

.three {
  animation-delay:5s;
  z-index:-1;
}

.four {
  animation-delay: 7.5s;
  z-index: -1;
}

@keyframes spin {
  0% {
      z-index:1;
  }

  33.33% {
      z-index:-1;
  }

  66.66% {
      z-index:-1;
  }
  100%{
      z-index:-1;
  }
}
.ft {
  background-color: #222831;
  height: 25% !important;
  display: flex;
  flex-wrap: wrap;
  color: white;
  text-transform: uppercase;
  /* padding-left: 42%; */
  justify-content: center;
  font-weight: bold;
  letter-spacing: 2px;
  box-shadow: 5px 5px 20px #eee;

}
.btn{
  margin: 0px 15px;
  font-weight: bold !important;
  font-family:monospace;
  font-size: 14pt !important;
}